/* keep the todo list in center */

body {
    text-align: center;
    height: 100%;
    background-image: url("data:image/jpeg;base64,");
    font-family: 'Galindo';
}

/* nav bar */
nav {
  text-align: center;
  background-color: rgb(72, 15, 226);
  color: white;
  margin: auto;
  height: 10%;
}
input[type=search] {
  width: 30%;
  font-size: 18px;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}
input[type=search]:focus {
  width: 100%;
}
/* dataEnterForm the todo list */
.dateSelect, .dataEnter {
    display: inline;
    padding: 5px;
}

a {
  color: white;
  text-decoration: none;
}
a:hover {
  background-color: gray;
}
/* ----------------------------- */
/* main */
main {
    margin-top: 12%;
}
form {
  margin: 5px;
}
#form div{
    padding: 5px;
}
#prioritySelect {
    margin: auto;
    width: 70%;
    padding: 5px;
}
.container {
    
    background-color: rgba(255, 255, 255, 0.5);
    border: 5px double cadetblue;
    border-radius: 20px;
}
/* ------------------------------ */
